
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default{
name:'themeList',
data(){
return{
theme_menu:[
{name:'全部',tab:'all'},
{name:'精华',tab:'good'},
{name:'分享',tab:'share'},
{name:'问答',tab:'ask'},
{name:'weex',tab:'weex'},
{name:'招聘',tab:'job'}
],
tab_index:0,
tab_name:'all',
page:1,
theme_list:[]
}
},
created(){
this.getThemeData('all',1,0);
},
methods:{
getThemeData(tab,page,index){
this.tab_name = tab;
this.tab_index = index;
this.page = page;
this.$axios.get('/api/v1/topics',{
params:{
tab:tab,
limit:15,
page:page
}
})
.then(result => {
if(result.status != 200){ return false; }
var res = result.data.data;
this.theme_list = res;
})
.catch(error => {
console.log(error)
})
},
tagDeal(data,top){
switch (data){
case 'share':
if(top){
return '置顶';
}else{
return '分享';
}
break;
case 'job':
return '招聘';
break;
case 'weex':
return 'weex';
break;
case 'ask':
return '问答';
break;
}
},
timeDeal(date){
var time_now = new Date().getTime();
var reply_time = new Date(date).getTime();
var time_interval = parseInt((time_now - reply_time)/1000);
if(time_interval<60){
return 1 + '分钟前';
}else if(time_interval<60*60){
return parseInt(time_interval/60) + '分钟前';
}else if(time_interval<60*60*24){
return parseInt(time_interval/(60*60)) + '小时前';
}else if(time_interval<60*60*24*30){
return parseInt(time_interval/(60*60*24)) + '天前';
}else if(time_interval<60*60*24*30*12){
return parseInt(time_interval/(60*60*24*30)) + '月前';
}else{
return parseInt(time_interval/(60*60*24*30*12)) + '年前';
}
}
},
components: {
Tab,
TabItem
}
}



